import BenefitsLabelData from './BenefitLabelData';
import actions from './actions';

const BenefitsLabel = new BenefitsLabelData().getAll();
const initState = {
  claimID: null,
  claimData:{},
  claimFileList:[],
  visitsData:[],
  billingFileList:[],
  documentFileList:[],
  tabIndex:null,
  billingInfo:{},
  exchangeRate: null,
  BenefitsLabel
};


export default function authReducer(state = initState, action) {

  switch (action.type) {

    case actions.CREATECLAIM_SUCCESS:
      return {
        ...state,
        claimID:action.payload.claimID,
        claimData: action.payload.claimData,
        claimFileList:action.payload.claimFileList,
        visitsData:action.payload.visitsData,
        tabIndex:'BillingButton',
       };
    case actions.INSERTBILLINGINFO_SUCCESS:

      var newBillingInfo = state.billingInfo;
      if (action.payload !== undefined) {
        newBillingInfo[action.payload.visitId] = action.payload.data
      }

      return {
        ...state,
        billingInfo:newBillingInfo,
        tabIndex:'DocumentsButton',
      };
    case actions.INSERTDOCUMENTS_SUCCESS:
      return {
        ...state,
       };
    case actions.RESETCLAIM:
      return { ...state, currentClaim: null };
    case actions.EXCHANGERATE_INIT_SUCCESS:
      return { ...state, exchangeRate: action.payload };
    case actions.SETTABINDEX_REQUEST:
      return { ...state, tabIndex: action.payload };
    default:
      return state;
  }
}
