const actions = {
  CREATECLAIM_REQUEST: 'INSERTCLAIM_REQUEST',
  CREATECLAIM_SUCCESS: 'CREATECLAIM_SUCCESS',
  CREATECLAIMINFO_REQUEST: 'CREATECLAIMINFO_REQUEST',
  RESETCLAIM: 'RESETCLAIM',
  EXCHANGERATE_INIT: 'EXCHANGERATE_INIT',
  EXCHANGERATE_INIT_SUCCESS:'EXCHANGERATE_INIT_SUCCESS',
  INSERTBILLINGINFO_REQUEST:'INSERTBILLINGINFO_REQUEST',
  INSERTBILLINGINFO_SUCCESS:'INSERTBILLINGINFO_SUCCESS',
  SETTABINDEX_REQUEST:'SETTABINDEX_REQUEST',
  INSERTDOCUMENTS_REQUEST:'INSERTDOCUMENTS_REQUEST',
  INSERTDOCUMENTS_SUCCESS:'INSERTDOCUMENTS_SUCCESS',
  addNewClaim: (payload) => {
    return (dispatch, getState) => {
      dispatch({
        type: actions.CREATECLAIM_REQUEST,
        payload
      });
    };
  },
  addClaimInfo: (payload) => {
    return (dispatch, getState) => {
      dispatch({
        type: actions.CREATECLAIMINFO_REQUEST,
        payload
      });
    };
  },
  insertBillingInfo: (payload) => {
    return (dispatch, getState) => {
      dispatch({
        type: actions.INSERTBILLINGINFO_REQUEST,
        payload
      });
    };
  },
  insertDocuments: (payload) => {

    return (dispatch, getState) => {
      dispatch({
        type: actions.INSERTDOCUMENTS_REQUEST,
        payload
      });
    };
  },
  resetClaim: () => {
    return (dispatch, getState) => {
      dispatch({
        type: actions.RESETCLAIM
      });
    };
  },
  exchangeRateInit: () => ({
    type: actions.EXCHANGERATE_INIT
  }),
  setTabIndex: (payload) => {
    return (dispatch, getState) => {
      dispatch({
        type: actions.SETTABINDEX_REQUEST,
        payload
      });
    };
  }
};
export default actions;
